<template>
	<fx-order-list-filter @on-back="onCloseClick">
		<van-cell-group>
			<w-cell title=""></w-cell>
			<w-cell title="" is-link @click="onBeginDateClick">{{beginDate}}</w-cell>
			<w-cell title="" is-link @click="onEndDateClick">{{endDate}}</w-cell>
			<w-cell title="" is-link @click="onRdcClick">{{rdc.name}}</w-cell>
			<w-cell title="" is-link @click="onInShopClick">{{inShop.name}}</w-cell>
			<w-cell title="" is-link @click="onHouseClick">{{house.name}}</w-cell>
			<w-cell title="" is-link @click="onGoodsClick">{{goods.name}}</w-cell>
			<w-cell
				title=""
				:is-link="!billType.id"
				:is-clear="!!billType.id"
				@click="onBillTypeClick"
				@on-clear="onBillTypeClearClick"
			>{{billType.name}}</w-cell>
			<van-pro-field
				ref="billNo"
				v-model="billNo"
				label=""
				placeholder=""
				input-align="right"
				@focus="onFocus('billNo')"
			></van-pro-field>
		</van-cell-group>
		<van-popup v-model="datePickerShow" position="bottom">
			<w-date-picker
				type="date"
				:visibleFlag.sync="datePickerShow"
				:currentDate="currentDate"
				@confirm="dateConfirmhandle"
			></w-date-picker>
		</van-popup>
		<van-popup v-model="rdcPopShow" position="right" class="fx-list-popup-300">
			<listComp
				:visibleFlag.sync="rdcPopShow"
				:data="rdcList"
				:loadFinished="true"
				title=""
				@on-select="selectRdcs"
				@on-search="searchRdcs"
			></listComp>
		</van-popup>
		<van-popup v-model="inShopPopShow" position="right" class="fx-list-popup-300">
			<listComp
				:visibleFlag.sync="inShopPopShow"
				:data="inShopList"
				title=""
				:loadFinished="true"
				@on-select="selectInShop"
				@on-search="searchInShop"
			></listComp>
		</van-popup>
		<van-popup v-model="housePopShow" position="right" class="fx-list-popup-300">
			<listComp
				:visibleFlag.sync="housePopShow"
				:data="houseList"
				title=""
				:loadFinished="true"
				@on-select="selectHouse"
				@on-search="searchHouse"
			></listComp>
		</van-popup>
		<van-popup v-model="goodsPopShow" position="right" class="fx-list-popup-300">
			<listComp
				:visibleFlag.sync="goodsPopShow"
				:data="goodsList"
				title=""
				:loading.sync="goodsLoading"
				:loadFinished="goodsLoadFinished"
				@on-select="selectGoods"
				@on-search="searchGoods"
				@on-load="loadGoodsList"
			></listComp>
		</van-popup>
		<van-popup v-model="billTypePopShow" position="right" class="fx-list-popup-300">
			<listComp
				:visibleFlag.sync="billTypePopShow"
				:data="billTypeList"
				title=""
				:loadFinished="true"
				@on-select="selectBillType"
				@on-search="searchBillType"
			></listComp>
		</van-popup>
		<w-button
			slot="bottom-btn"
			size="large"
			type="info"
			@click="onResetClick"
		></w-button>
		<w-button
			slot="bottom-btn"
			size="large"
			type="primary"
			@click="onConfirmClick"
		></w-button>
	</fx-order-list-filter>
</template>

<script>
import { mapState } from 'vuex'
import listComp from '@/components/common/ListComp.vue'
const ITEM_SIZE = 50
export default {
	name: 'FilterPop',
	components: {
		listComp
	},
	props: {
		visibleFlag: Boolean,
		filterParams: {
			type: Object,
			default: () => {
				return {}
			}
		}
	},
	data () {
		return {
			leftOptions: {
				showBackBtn: true,
				preventGoBack: true
			},
			currentDate: new Date(),
			datePickerShow: false,
			beginDate: '',
			endDate: '',
			rdc: {
				name: ''
			},
			rdcList: [],
			rdcPopShow: false,
			inShop: {
				name: ''
			},
			inShopList: [],
			inShopPopShow: false,
			house: {
				name: ''
			},
			houseList: [],
			housePopShow: false,
			goods: {
				name: ''
			},
			goodsListStart: 0,
			goodsList: [],
			goodsLoading: false,
			goodsLoadFinished: false,
			goodsPopShow: false,
			searchGoodsKeyword: '',
			billType: {
				name: ''
			},
			billTypeList: [],
			billTypePopShow: false,
			billNo: ''
		}
	},
	watch: {
		visibleFlag: {
			handler (val) {
				if (val && this.$fxUtils.isEmptyObjSHW(this.filterParams)) {
					this.resetHandler()
				}
			},
			immediate: true
		},
		businessDateStart: {
			immediate: true,
			handler (val) {
				this.beginDate = val
			}
		},
		businessDateEnd: {
			immediate: true,
			handler (val) {
				this.endDate = val
			}
		}
	},
	computed: {
		...mapState({
			loginerShopId: state => state.loginer.loginerShopId,
			loginerId: state => state.loginer.loginerId,
			businessDateStart: state => state.systemParamsCld.businessDateStart,
			businessDateEnd: state => state.systemParamsCld.businessDateEnd
		})
	},
	methods: {
		onCloseClick () {
			this.$emit('update:visibleFlag', false)
		},
		onBeginDateClick () {
			this.datePickerShow = true
			if (this.beginDate) this.currentDate = new Date(this.beginDate)
			this.dateType = 'begin'
		},
		onEndDateClick () {
			this.datePickerShow = true
			if (this.endDate) this.currentDate = new Date(this.endDate)
			this.dateType = 'end'
		},
		dateConfirmhandle (date) {
			if (this.dateType === 'begin') {
				this.beginDate = this.$fxUtils.setDate(date)
			} else {
				this.endDate = this.$fxUtils.setDate(date)
			}
			this.datePickerShow = false
		},
		onRdcClick () {
			this.getRdcListData().then(res => {
				this.rdcList = res.shops
				this.rdcPopShow = true
			})
		},
		selectRdcs (item) {
			this.rdc = item
		},
		searchRdcs (keyword) {
			this.getRdcListData(keyword).then(res => {
				this.rdcList = res.shops
			})
		},
		getRdcListData (keyword = null) {
			return new Promise(resolve => {
				let params = {
					keyword: keyword
				}
				this.$fxHttp.requestGet({
					url: `${this.$fxBaseApi.url}/accounts/${this.loginerShopId}/users/${this.loginerId}/rdcs`,
					data: params
				}).then(res => {
					resolve(res)
				})
			})
		},
		onInShopClick () {
			this.getInShopListData().then(res => {
				this.inShopList = res.shops
				this.inShopPopShow = true
			})
		},
		selectInShop (item) {
			this.inShop = item
		},
		searchInShop (keyword) {
			this.getInShopListData(keyword).then(res => {
				this.inShopList = res.shops
			})
		},
		getInShopListData (keyword = null) {
			return new Promise(resolve => {
				let params = {
					m: 'list4ShopOrderBill',
					keyword: keyword
				}
				this.$fxHttp.requestGet({
					url: `${this.$fxBaseApi.url}/accounts/${this.loginerShopId}/users/${this.loginerId}/shops`,
					data: params
				}).then(res => {
					resolve(res)
				})
			})
		},
		onHouseClick () {
			if (!this.inShop.id) {
				this.$fxToast.fail('!')
				return false
			}
			this.getHouseListData().then(res => {
				this.houseList = res.shops
				this.housePopShow = true
			})
		},
		selectHouse (item) {
			this.house = item
		},
		searchHouse (keyword) {
			this.getHouseListData(keyword).then(res => {
				this.houseList = res.shops
			})
		},
		getHouseListData (keyword = null) {
			return new Promise(resolve => {
				let params = {
					keyword: keyword
				}
				this.$fxHttp.requestGet({
					url: `${this.$fxBaseApi.url}/accounts/${this.inShop.id}/users/${this.loginerId}/houses`,
					data: params
				}).then(res => {
					resolve(res)
				})
			})
		},
		onGoodsClick () {
			this.goodsListStart = 0
			this.searchGoodsKeyword = ''
			this.getGoodsListData().then(res => {
				this.goodsLoading = false
				this.goodsPopShow = true
				this.goodsList = res.goods
			})
		},
		selectGoods (item) {
			this.goods = item
		},
		searchGoods (keyword) {
			this.goodsListStart = 0
			this.searchGoodsKeyword = keyword
			this.getGoodsListData(keyword).then(res => {
				if (res.goods.length < ITEM_SIZE) {
					this.goodsLoadFinished = true
				}
				this.goodsList = res.goods
			})
		},
		loadGoodsList () {
			if (this.goodsLoadFinished === true) {
				return
			}
			this.goodsListStart = this.goodsListStart + ITEM_SIZE
			this.getGoodsListData(this.searchGoodsKeyword).then(res => {
				this.goodsLoading = false
				if (res.goods.length < ITEM_SIZE) {
					this.goodsLoadFinished = true
				}
				this.goodsList.push(...res.goods)
			})
		},
		getGoodsListData (keyword = null) {
			return new Promise(resolve => {
				let params = {
					start: this.goodsListStart,
					size: ITEM_SIZE,
					paged: true,
					m: 'list4ShopOrderBill',
					keyword: keyword
				}
				this.$fxHttp.requestGet({
					url: `${this.$fxBaseApi.url}/accounts/${this.loginerShopId}/users/${this.loginerId}/goods`,
					data: params
				}).then(res => {
					resolve(res)
				})
			})
		},
		onBillTypeClick () {
			this.getBillTypeListData().then(res => {
				this.billTypeList = res.businessTypes
				this.billTypePopShow = true
			})
		},
		selectBillType (item) {
			this.billType = item
		},
		searchBillType (keyword) {
			this.getBillTypeListData(keyword).then(res => {
				this.billTypeList = res.businessTypes
			})
		},
		getBillTypeListData (keyword = null) {
			return new Promise(resolve => {
				let params = {
					m: 'list4DistributionOutListBill',
					keyword: keyword
				}
				this.$fxHttp.requestGet({
					url: `${this.$fxBaseApi.url}/accounts/${this.loginerShopId}/users/${this.loginerId}/businessTypes`,
					data: params
				}).then(res => {
					resolve(res)
				})
			})
		},
		onResetClick () {
			this.resetHandler()
		},
		resetHandler () {
			this.beginDate = this.businessDateStart
			this.endDate = this.businessDateEnd
			this.rdc = {
				name: ''
			}
			this.inShop = {
				name: ''
			}
			this.house = {
				name: ''
			}
			this.billType = {
				name: ''
			}
			this.goods = {
				name: ''
			}
			this.billNo = ''
		},
		onConfirmClick () {
			if (new Date(this.endDate) < new Date(this.beginDate)) {
				this.$fxToast.fail('!')
				return false
			}
			this.$emit('update:visibleFlag', false)
			this.$emit('update:filterParams', this.getFilterParams())
			this.$emit('on-confirm', this.getFilterParams())
		},
		getFilterParams () {
			return {
				beginDate: this.beginDate,
				endDate: this.endDate,
				rdcId: this.rdc.id,
				inShopId: this.inShop.id,
				houseId: this.house.id,
				billTypeId: this.billType.id,
				goodsId: this.goods.id,
				billNo: this.billNo
			}
		},
		onFocus (ref) {
			this.onScrollTop(ref)
		},
		onScrollTop (scrollRef, distance = 20) {
			let detailsRowRef = this.$refs[scrollRef]
			if (!detailsRowRef || detailsRowRef.length === 0) {
				return false
			}
			let offsetTop = detailsRowRef.$el.offsetTop
			let $el = this.$refs['filterContent']
			this.$fxUtils.scrollTopW({ offsetTop, $el, distance })
		},
		onBillTypeClearClick () {
			this.billType = {
				name: '',
				id: ''
			}
		}
	}
}
</script>
